<template>
  <v-layout
    row
    justify-center>
    <v-flex md12>
      <v-card class="elevation-0">
        <v-card-text>
          <v-toolbar
            flat
            color="white">
            <v-toolbar-title>Contas Bancárias Prático</v-toolbar-title>
            <v-divider
              class="mx-2"
              inset
              vertical
            />
            <v-spacer />
            <v-btn
              small
              fab
              color="primary">
              <v-icon @click="newItem()">mdi-plus</v-icon>
            </v-btn>
          </v-toolbar>
          <v-progress-linear
            :active="loading"
            color="light-blue"
            indeterminate
            height="3"
          />
          <v-data-table
            :headers="headers"
            :items="items"
            class="elevation-1 arrow-pagintion"
            hide-actions
          >
            <template
              slot="items"
              slot-scope="{ item }"
            >
              <td
                v-if="!!item.principal"
                class="text-xs-center">
                <v-icon color="success">mdi-check-bold</v-icon>
              </td>
              <td v-else />
              <td>
                {{ item.bancoDescricao }}
              </td>
              <td class="text-xs-right">
                {{ item.agencia }}
              </td>
              <td class="text-xs-right">
                {{ item.digitoAgencia }}
              </td>
              <td class="text-xs-right">
                {{ item.conta }}
              </td>
              <td class="text-xs-right">
                {{ item.digitoConta }}
              </td>
              <td class="text-xs-right">
                {{ item.carteira }}
              </td>
              <td>
                {{ item.tpContaDescricao }}
              </td>
              <td class="justify-end layout ma-2">
                <v-icon
                  class="mr-2"
                  color="blue"
                  title="Editar conta"
                  @click="editItem(item)"
                >mdi-pencil</v-icon>
                <v-icon
                  color="red"
                  title="Excluir conta"
                  @click="openDialogDelete(item)"
                >mdi-delete</v-icon>
              </td>
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
    </v-flex>
    <template
      v-if="dialog">
      <v-layout
        row
        justify-center>
        <v-dialog
          v-model="dialog"
          max-width="600">
          <v-form
            ref="form2"
            @submit.prevent="validateBeforeSubmit">
            <v-card>
              <v-card-title class="title">
                Dados da Conta
              </v-card-title>
              <v-card-text>
                <v-layout
                  row
                  wrap>
                  <v-flex md6>
                    <v-autocomplete
                      v-validate="'required'"
                      v-model="form.tpConta"
                      :items="tiposConta"
                      :error-messages="errors.collect('Tipo Conta')"
                      item-value="id"
                      item-text="descricao"
                      data-vv-name="Tipo Conta"
                      label="Tipo Conta"
                    />
                  </v-flex>
                  <v-flex md6>
                    <v-autocomplete
                      v-validate="'required'"
                      v-model="form.banco"
                      :items="bancos"
                      :error-messages="errors.collect('Banco')"
                      item-value="id"
                      item-text="descricao"
                      data-vv-name="Banco"
                      label="Banco"
                    />
                  </v-flex>
                  <v-flex md2>
                    <v-text-field
                      v-validate="'required'"
                      v-model="form.agencia"
                      :error-messages="errors.collect('Agência')"
                      data-vv-name="Agência"
                      label="Agência"
                    />
                  </v-flex>
                  <v-flex md3>
                    <v-text-field
                      v-model="form.digitoAgencia"
                      label="Dígito Agência"
                    />
                  </v-flex>
                  <v-flex md2>
                    <v-text-field
                      v-validate="'required'"
                      v-model="form.conta"
                      :error-messages="errors.collect('Conta')"
                      data-vv-name="Conta"
                      label="Conta"
                    />
                  </v-flex>
                  <v-flex md3>
                    <v-text-field
                      v-validate="'required'"
                      v-model="form.digitoConta"
                      :error-messages="errors.collect('Dígito Conta')"
                      data-vv-name="Dígito Conta"
                      label="Dígito Conta"
                    />
                  </v-flex>
                  <v-flex md2>
                    <v-text-field
                      v-model="form.carteira"
                      label="Carteira"
                      type="text"
                    />
                  </v-flex>
                  <v-flex md12>
                    <v-checkbox
                      v-model="form.principal"
                      label="Conta Principal"
                      title="A conta principal, será utilizada nas TEDs em lote e geração de boletos quando nenhuma conta for definida."
                    />
                  </v-flex>
                </v-layout>
              </v-card-text>
              <v-card-actions>
                <v-btn
                  outline
                  style="color: black !important"
                  @click="cancelar()">Cancelar</v-btn>
                <v-spacer />
                <v-btn
                  :loading="loadingBtn"
                  type="submit"
                  color="primary"
                >Salvar</v-btn>
              </v-card-actions>
            </v-card>
          </v-form>
        </v-dialog>
        <core-progress-modal
          :show="loading" />
      </v-layout>
    </template>
    <template>
      <v-layout
        row
        justify-center>
        <v-dialog
          v-model="dialogDelete"
          max-width="390">
          <v-card>
            <v-card-title class="headline">Excluir Conta Bancária</v-card-title>
            <v-card-text>Confirma a exclusão dessa conta bancária, essa operação não poderá ser desfeita?</v-card-text>
            <v-card-actions>
              <v-spacer />
              <v-btn
                outline
                style="color: black !important"
                @click="closeDialogDelete">Cancelar
              </v-btn>
              <v-btn
                color="red"
                @click="deleteItem">Excluir</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-layout>
    </template>
  </v-layout>
</template>
<script>
import { TiposBusiness, BeneficiarioBusiness } from '../../business'

export default {
  data: () => ({
    loading: false,
    loadingBtn: false,
    dialog: false,
    dialogDelete: false,
    item: null,
    items: [],
    bancos: [],
    tiposConta: [],
    form: {
      id: null,
      tpConta: null,
      banco: null,
      agencia: null,
      digitoAgencia: null,
      conta: null,
      digitoConta: null,
      carteira: null
    },
    headers: [
      {
        sortable: false,
        text: 'Principal',
        value: 'principal',
        align: 'center'
      },
      {
        sortable: false,
        text: 'Banco',
        value: 'banco'
      },
      {
        sortable: false,
        text: 'Agência',
        value: 'agencia',
        align: 'right'
      },
      {
        sortable: false,
        text: 'Dígito Agência',
        value: 'digitoAgencia',
        align: 'right'
      },
      {
        sortable: false,
        text: 'Conta',
        value: 'conta',
        align: 'right'
      },
      {
        sortable: false,
        text: 'Dígito Conta',
        value: 'digitoConta',
        align: 'right'
      },
      {
        sortable: false,
        text: 'Carteira',
        value: 'carteira',
        align: 'right'
      },
      {
        sortable: false,
        text: 'Tipo Conta',
        value: 'tpConta'
      },
      {
        sortable: false,
        text: '',
        value: 'acoes'
      }
    ]
  }),
  beforeMount () {
    this.loading = true
    TiposBusiness.listBancos()
      .then(response => {
        this.bancos = response.data
      }).finally(() => {
        this.loading = false
      })
    TiposBusiness.listTiposContaBanco()
      .then(response => {
        this.tiposConta = response.data
      }).finally(() => {
        this.loading = false
      })
    this.loadContas()
  },
  methods: {
    validateBeforeSubmit () {
      this.$validator.validate().then(result => {
        if (result) {
          this.loadingBtn = true
          BeneficiarioBusiness.salvar(this.form).then((response) => {
            this.$root.showSucesso('Dados enviados com sucesso')
            this.cancelar()
            this.loadContas()
          }).catch(() => this.$root.showErro('Erro ao cadastrar'))
            .finally(() => {
              this.loadingBtn = false
            })
        } else {
          this.$root.showAlerta(this.$vuetify.t('$vuetify.Erro.camposObrigatorios'))
        }
      })
    },
    loadContas () {
      this.loading = true
      BeneficiarioBusiness.findAllBanco()
        .then(response => {
          this.items = response.data
        }).finally(() => {
          this.loading = false
        })
    },
    newItem () {
      this.resetForm()
      this.dialog = true
    },
    editItem (item) {
      this.form = { ...item }
      this.dialog = true
    },
    cancelar () {
      this.dialog = false
      this.resetForm()
    },
    closeDialogDelete () {
      this.dialogDelete = false
      this.resetForm()
    },
    openDialogDelete (item) {
      this.form = { ...item }
      this.dialogDelete = true
    },
    deleteItem () {
      this.loading = true
      BeneficiarioBusiness.delete(this.form.id)
        .then(response => {
          this.$root.showSucesso('Conta excluída')
          this.closeDialogDelete()
          this.loadContas()
          this.loading = false
        }).catch(erro => {
          this.$root.showErro('Erro ao tentar excluir a conta bancária.')
          this.closeDialogDelete()
          this.loading = false
        })
    },
    resetForm () {
      this.form = {
        id: null,
        tpConta: null,
        banco: null,
        agencia: null,
        digitoAgencia: null,
        conta: null,
        digitoConta: null,
        carteira: null
      }
    }
  }
}
</script>
